/* Estilos globais da aplicação */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Arial', sans-serif;
    line-height: 1.6;
    color: #333;
}

/* Header */
header {
    background-color: #1068cc;
    color: white;
    padding: 1rem 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
}

.logo img {
    height: 50px;
}

.menu-principal ul {
    list-style: none;
    display: flex;
    gap: 2rem;
}

.menu-principal a {
    color: white;
    text-decoration: none;
    padding: 0.5rem 1rem;
    border-radius: 4px;
    transition: background-color 0.3s;
}

.menu-principal a:hover,
.menu-principal a.active {
    background-color: #34495e;
}

/* Main content */
main {
    min-height: calc(100vh - 140px);
    padding: 2rem;
}

.conteudo-principal {
    max-width: 1200px;
    margin: 0 auto;
}

.conteudo-principal h1 {
    color: #2c3e50;
    margin-bottom: 1rem;
}

.conteudo-principal h2 {
    color: #34495e;
    margin-bottom: 1rem;
}

/* Carrossel e eventos */
.carrosel, .eventos {
    background-color: #ecf0f1;
    padding: 2rem;
    margin: 1rem 0;
    border-radius: 8px;
}

/* Grids */
.marcas-grid, .galeria-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 1rem;
    margin-top: 2rem;
}

.marca-item, .foto-item {
    background-color: #3498db;
    color: white;
    padding: 2rem;
    text-align: center;
    border-radius: 8px;
    font-weight: bold;
}

/* Footer */
footer {
    background-color: #3089e2;
    color: white;
    padding: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.redeSocial {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.redeSocial img {
    width: 40px;
    height: 40px;
    transition: transform 0.3s;
}

.redeSocial img:hover {
    transform: scale(1.1);
}

.logoFinal img {
    height: 40px;
}

/* Responsive */
@media (max-width: 768px) {
    header {
        flex-direction: column;
        gap: 1rem;
    }
    
    .menu-principal ul {
        flex-direction: column;
        text-align: center;
        gap: 0.5rem;
    }
    
    footer {
        flex-direction: column;
        gap: 1rem;
        text-align: center;
    }
}
